.cart {
  background-color: white;
}
.cart-banner {
  width: 100vw;
  max-height: 20vh;
}
.cart-heading {
  border-bottom: 2px solid grey;
  padding: 2%;
  display: flex;
  justify-content: space-between;
}
.empty-cart-icon {
  text-align: center;
  font-size: 10em;
  color: rgba(107, 107, 207, 0.308);
}
.cart-item {
  display: flex;
  align-items: center;
  width: 100vw;
  background-color: white;
  padding: 2%;
  margin-bottom: 0.5%;
  border-bottom: 2px solid rgb(182, 182, 182);
}

.cart-item-details {
  min-height: 150px;
  margin-left: 0.5%;
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  align-self: flex-start;
}
.cart-item-img {
  max-width: 200px;
  margin-right: 5%;
}

.change-quantity-btn {
  background-color: rgb(209, 209, 209);
  font-size: medium;
  color: black;
  width: 25px;
  height: 25px;
}

.remove-item-btn {
  background-color: rgba(255, 0, 0, 0.608);
  color: black;
  font-size: small;
  padding: 10px 20px;
  align-self: center;
  font-weight: 800;
  cursor: pointer;
}
